<template>
  <div class="mainpage" autocomplete="off" autocorrect="off" autocapitalize="off" spellcheck="false">

    <h3>Spend funds from a multisig address.</h3>
    <br>
    <a v-if="toggleCamera">Scan</a><a v-else>Enter</a> your redeem script or transaction:

    <b-row>
      <b-col>
          <b-form-input
            v-model="receivedRedeemInput"
            class="inputField"
            :state="isValidRedeemInput"
          ></b-form-input>
      </b-col>
      <b-col class="clipboard-button">
        <b-button
          variant="outline-primary"
          class="inputField"
          :pressed.sync="toggleCamera"
          v-clipboard:copy=""
        >📷</b-button>
      </b-col>
    </b-row>

    <div v-if="redeemInputValidationError" style="color: darkred;">  <!-- conditional rendering -->
      {{redeemInputValidationError}}
    </div> <!-- conditional rendering -->

    <div v-if="toggleCamera && receivedRedeemInputState === null"> <!-- conditional rendering -->

      <div v-if="cameraError"> <!-- conditional rendering -->
        <p style="color: darkred">{{cameraError}}</p>
      </div> <!-- conditional rendering -->

      <qrcode-stream v-else
        @decode="onCameraDecode"
        @init="onCameraInit"
        class="inputField"
      /> <!-- conditional rendering -->

    </div> <!-- conditional rendering -->


    <div v-if="receivedRedeemInputState === true"> <!-- conditional rendering -->

      <!-- <b>You are starting a new transaction.</b>
      <br> -->
      Corresponding multisig address:

      <div class="inputField">
        <b-form-input
          style="text-align: center;"
          readonly
          type="text"
          v-model="receivedRedeemScriptAddress"
        >
        </b-form-input>
      </div>

      Possible signers:

      <div class="inputField">
        <b-form-input v-for="n in receivedRedeemScriptInvolvedAddresses.length"
          class="inputField"
          style="text-align: center;"
          readonly
          type="text"
          v-model="receivedRedeemScriptInvolvedAddresses[n-1].address"
          :key="n"
        >
        </b-form-input>
      </div>

      <div class="inputField">
        Required signatures: {{receivedRedeemScriptRequiredSignatures}}
      </div>

      <div v-if="signedSoFar.length > 0"> <!-- conditional rendering -->

        Who signed so far:

        <div class="inputField">
          <b-form-input v-for="n in signedSoFar.length"
            class="inputField"
            style="text-align: center;"
            readonly
            type="text"
            v-model="signedSoFar[n-1].address"
            :key="n"
          >
          </b-form-input>
        </div>

      </div> <!-- conditional rendering -->

      <div v-if="!showFromExplorer || isFullySigned"> <!-- conditional rendering -->

        Destination address:

        <div class="inputField">
          <b-form-input v-for="n in destinationAddresses.length"
            class="inputField"
            style="text-align: center;"
            readonly
            type="text"
            v-model="destinationAddresses[n-1]"
            :key="n"
          >
          </b-form-input>
        </div>

        Amount being sent:

        <div class="inputField">
          <b-form-input
            style="text-align: center;"
            readonly
            type="text"
            v-model="sendingAmount"
          >
          </b-form-input>
        </div>

      </div> <!-- conditional rendering -->

      <div v-if="isFullySigned" class="inputField"> <!-- conditional rendering -->

        <br>This transaction has been fully signed and is ready to be broadcast.

        <p style="font-size: small;"><a
          :href="'https://deckersu.github.io/coinbin/?verify=' + receivedRedeemInput"
          target="_blank"
        >
          Validate on third-party software.
        </a></p>

      </div> <!-- conditional rendering -->

      <div v-if="showFromExplorer === true || isFullySigned" class="inputField"> <!-- conditional rendering -->

        Insight explorer:

        <div>
          <b-form-input
            v-model="insightExplorerUrl"
            placeholder="Example: https://KMD.explorer.dexstats.info/"
            class="inputField"
            :state="isValidUrl"
          ></b-form-input>
        </div>

      </div> <!-- conditional rendering -->

      <div v-if="isFullySigned && isValidUrl"> <!-- conditional rendering -->

        <b-button
          class="inputField"
          size="lg"
          variant="success"
          :pressed.sync="broadcastButtonIsPressed"
          :disabled="broadcastButtonIsPressed"
          @click="sendTransaction()"
        >
          Broadcast
        </b-button>

        <div v-if="sentTransactionId"> <!-- conditional rendering -->
          Hooray!
          <h1 style="margin-bottom: 0;">🎉</h1>
          Your transaction made it to the network!
          <p style="font-size: small;"><a
            :href="insightExplorerUrlWSlash + 'tx/' + sentTransactionId"
            target="_blank"
          >
            See it on the explorer.
          </a></p>
        </div> <!-- conditional rendering -->

        <div v-else-if="sendTransactionErrorMessage"> <!-- conditional rendering -->
          <p style="color: darkred">{{sendTransactionErrorMessage}}</p>
        </div> <!-- conditional rendering -->

      </div> <!-- conditional rendering -->

      <div v-if="showFromExplorer === true" class="inputField"> <!-- conditional rendering -->

        <div class="inputField" style="color: gray;">
          <b-form-checkbox
            v-model="selectedNetwork"
            value="komodo_nonsapling"
            unchecked-value="komodo"
          >
            This is a non-sapling chain (check for OOT/ZILLA).
          </b-form-checkbox>
        </div>

        <div v-if="calculatedBalance === null && axiosErrorMessage === null"> <!-- conditional rendering -->
          Retrieving balance...
        </div>

        <div v-else-if="axiosErrorMessage"> <!-- conditional rendering -->
          <p style="color: darkred">{{axiosErrorMessage}}</p>
        </div>

        <div v-else-if="calculatedBalance > 0"> <!-- conditional rendering -->

          Your balance:

          <div class="inputField">
            <b-row>
              <b-col>
                <b-form-input
                  readonly
                  style="text-align: center;"
                  type="text"
                  v-model="calculatedBalance"
                >
                </b-form-input>
              </b-col>
              <b-col class="clipboard-button">
                <b-button variant="outline-primary"
                  v-clipboard:copy="calculatedBalance"
                >📋</b-button>
              </b-col>
            </b-row>
          </div>

          Please input desired amount:

          <b-form-input
            v-model="selectedAmount"
            class="inputField"
            type="number"
            placeholder="0.01"
            :state="isValidAmount"
          >
          </b-form-input>

          Destination address:

          <b-form-input
            v-model="destinationAddress"
            class="inputField"
            :state="isValidDestinationAddr"
          >
          </b-form-input>

        </div> <!-- conditional rendering -->

        <div v-else-if="calculatedBalance === 0"> <!-- conditional rendering -->
          Balance: 0
        </div> <!-- conditional rendering -->

      </div> <!-- conditional rendering -->

      <div v-if="showFromExplorer && isValidAmount && isValidDestinationAddr"> <!-- conditional rendering -->

        <br>
        <h4 class="inputField">Your transaction is ready to be signed:</h4>

        <show-qr
          :string="getTransaction"
          :stringCompressed="getTransactionCompressed"
          :shareLink="origin + '/?r=' + getTransactionCompressed"
          :showValidator="true"
        />

        <div style="align: center; margin-top: 20px;">
          <b-button
          variant="outline-primary"
          pill
          @click="receivedRedeemInput = ''"
          >Done for now, clear the form.</b-button>
        </div>

      </div> <!-- conditional rendering -->

      <div v-if="!isFullySigned && !showFromExplorer"> <!-- conditional rendering -->

        <br>
        <b-form-group label="What to do next?">
          <b-form-radio-group
            v-model="radioSelected"
            :options="radioOptions"
            stacked
            buttons
            size="lg"
            button-variant="outline-primary"
          ></b-form-radio-group>
        </b-form-group>
        <br>

        <div v-if="radioSelected === 'show_qr'">

          <show-qr
            :string="receivedRedeemInput"
            :stringCompressed="hexToBase64(receivedRedeemInput)"
            :shareLink="origin + '/?r=' + hexToBase64(receivedRedeemInput)"
            :showValidator="true"
          />

          <div style="align: center; margin-top: 20px;">
            <b-button
            variant="outline-primary"
            pill
            @click="receivedRedeemInput = ''"
            >Done for now, clear the form.</b-button>
          </div>

        </div>


        <div v-else-if="radioSelected === 'sign_tx'">

          <div class="securityRecommendations">
            <a style="color: red;"><b>Security recommendation:</b></a><br>
            <br>
            Never expose your private key to devices that<br>
            have access to the internet or any network.<br>
            You should create, store and use your private key, only<br>
            in a secure, <b>forever offline</b>, device.<br>
            <br>
            This application provides QR codes to facilitate the<br>
            transmission of data back and forth between online and <br>
            offline devices.<br>
            <br>
            If this device has internet access, or will have in the future,<br>
            then you're doing it wrong; proceed at your own risk.<br>
            <br>
          </div>

          Insert your private key (WIF):

          <b-form-input
            v-model="wif"
            class="inputField"
            type="password"
            :state="isValidWif"
          >
          </b-form-input>

          <div v-if="isValidWif && !signatureErrorMsg"> <!-- conditional rendering -->

            <br>
            <div v-if="isCompleteNow">
              <h4 class="inputField">Signature added. Ready to be broadcast:</h4>
            </div>
            <div v-else>
              <h4 class="inputField">Signature added to transaction:</h4>
            </div>

            <show-qr
              :string="getTransaction"
              :stringCompressed="getTransactionCompressed"
            />

            <div style="align: center; margin-top: 20px;">
              <b-button
              variant="outline-primary"
              pill
              @click="receivedRedeemInput = ''"
              >Done for now, clear the form.</b-button>
            </div>

          </div> <!-- conditional rendering -->

          <div v-if="signatureErrorMsg" style="color: darkred"> <!-- conditional rendering -->
            {{signatureErrorMsg}}
          </div> <!-- conditional rendering -->

        </div> <!-- conditional rendering -->

      </div>

    </div> <!-- conditional rendering -->

  </div>
</template>


<!-- script section -->
<script>
import bitGoUTXO from 'bitgo-utxo-lib'
import coinSelect from 'coinselect'
import { QrcodeStream } from 'vue-qrcode-reader'
import * as pako from 'pako'
const axios = require('axios');
const _ = require('lodash');
import ShowQr from '@/components/ShowQr.vue'


export default {
  name: 'RedeemTab',

  props: {
    msg: String,
    defaultNetwork: String,
  },

  components: {
    QrcodeStream,
    ShowQr,
  },

  data() {
    return {
      receivedRedeemInput: '',
      receivedRedeemInputState: null,
      receivedRedeemScript: null,
      receivedRedeemScriptAddress: null,
      receivedRedeemScriptInvolvedAddresses: [],
      receivedRedeemScriptRequiredSignatures: null,
      insightExplorerUrl: 'https://kmd.explorer.dexstats.info/',
      showFromExplorer: null,
      utxos: null,
      axiosErrorMessage: null,
      selectedAmount: null,
      destinationAddress: '',
      wif: '',
      selectedNetwork: null,
      signatureErrorMsg: null,
      signedSoFar: [],
      isCompleteNow: null,
      isFullySigned: null,
      destinationAddresses: [],
      sendingAmount: null,
      sentTransactionId: null,
      sendTransactionErrorMessage: null,
      broadcastButtonIsPressed: false,
      broadcasting: false,
      cameraError: null,
      toggleCamera: false,
      radioOptions: [
        {
          text: 'This is my online device, show me the QR code.',
          value: 'show_qr',
        },
        {
          text: 'This is my offline device, let\'s sign this.',
          value: 'sign_tx',
        }
      ],
      radioSelected: null,
    }
  },

  created: function() {
    this.selectedNetwork = this.defaultNetwork
  },

  mounted: function() {
    try {
      this.receivedRedeemInput = this.base64ToHex(this.getUrlParameter('r'))
    } catch {
      // pass
    }
  },

  watch: {
    showFromExplorer: function() {
      let vm = this
      if (vm.showFromExplorer === true) {
        vm.getUtxos()
      }
    },
    selectedNetwork: function() {
      let vm = this
      vm.network = bitGoUTXO.networks[vm.selectedNetwork]
    }
  },

  methods: {
    getUrlParameter(param) {
      let result = null,
        tmp = [];
      let items = location.search.substr(1).split("&");
      for (let index = 0; index < items.length; index++) {
        tmp = items[index].split("=");
        if (tmp[0] === param) result = decodeURIComponent(tmp[1]);
      }
      return result;
    },
    getAddress(pubkeyHex) {
      let vm = this
      let pubkeyBuffer = Buffer.from(pubkeyHex, 'hex')
      let address = bitGoUTXO.ECPair
        .fromPublicKeyBuffer(pubkeyBuffer, vm.network).getAddress()
      return(address)
    },
    getScriptAddress(scriptBuffer) {
      let vm = this
      let scriptPubKey = bitGoUTXO.script.scriptHash.output.encode(
        bitGoUTXO.crypto.hash160(scriptBuffer)
      )
      let address = bitGoUTXO.address.fromOutputScript(
        scriptPubKey,
        vm.network
      )
      return address
    },
    getMultiSig(requiredSignersNum, pubkeysHex) {
      let vm = this
      let pubkeys = pubkeysHex.map(
        function (hex) { return Buffer.from(hex, 'hex') }
      )
      let redeemScript = bitGoUTXO.script.multisig.output.encode(
        requiredSignersNum,
        pubkeys
      )
      let address = vm.getScriptAddress(redeemScript)
      let redeemScriptHex = redeemScript.toString('hex')
      return {
        'redeemScript': redeemScriptHex,
        'address': address,
      }
    },
    redeemScriptNSignersSanityCheck(decompiledRedeemScript) {
      // get number of involved pubkeys
      let scriptLength = decompiledRedeemScript.length
      let npksOp = scriptLength - 2
      let nPubkeys = decompiledRedeemScript[npksOp] - 80
      // check if there are more items than expected,
      // to avoid, for example, same script pasted twice
      let nItems = scriptLength - 3
      if ( nItems !== nPubkeys ) {
        return false
      } else {
        return true
      }
    },
    storeReceivedRedeemScriptAddresses(decompiledRedeemScript) {
      let vm = this
      // read contained public keys
      let scriptLength = decompiledRedeemScript.length
      for (let i = 1; i < (scriptLength - 2); i++) {
        let pubkeyHex = decompiledRedeemScript[i].toString('hex')
        // sanity check
        if (pubkeyHex.length !== 66) {
          throw ("Error: one of the public keys has a wrong length!")
        } else {
          // get and store address and pubkey
          let addressAndPubkey = {
            address: vm.getAddress(pubkeyHex),
            pubkey: pubkeyHex,
          }
          vm.receivedRedeemScriptInvolvedAddresses.push(addressAndPubkey)
        }
      }
      vm.receivedRedeemScriptRequiredSignatures = decompiledRedeemScript[0] - 80
      return true
    },
    getUtxos: _.debounce(function() {
      let vm = this
      // reset variables
      vm.utxos = null
      vm.axiosErrorMessage = null
      // add trailing slash if missing
      let explorerUrl = vm.insightExplorerUrlWSlash
      // define url
      let url = (
        explorerUrl +
        'insight-api-komodo/addr/' +
        vm.receivedRedeemScriptAddress +
        '/utxo'
      )
      // make http request
      axios.get(url)
        .then(function (response) {
          vm.utxos = response.data
        })
        .catch(function (error) {
          vm.axiosErrorMessage = error;
        })
    }, 1000),
    sendTransaction: _.debounce(function() {
      let vm = this
      // don't repeat this operation if already running
      if (vm.broadcasting) return
      // reset variables
      vm.sentTransactionId = null
      vm.sendTransactionErrorMessage = null
      // add trailing slash if missing
      let explorerUrl = vm.insightExplorerUrlWSlash
      // define url
      let url = explorerUrl + 'insight-api-komodo/tx/send'
      // make http request
      vm.broadcasting = true
      axios.post(url, {'rawtx': vm.receivedRedeemInput})
        .then(function (response) {
          vm.broadcastButtonIsPressed = vm.broadcasting = false  // release button
          vm.sentTransactionId = response.data.txid
        })
        .catch(function (error) {
          vm.broadcastButtonIsPressed = vm.broadcasting = false  // release button
          vm.sendTransactionErrorMessage = error.response.data || error;
        })
    }, 500),
    isValidAddr(addr) {
      let vm = this
      if (addr === '') {
        return null
      }
      try {
        bitGoUTXO.address.toOutputScript(addr, vm.network)
        return true
      } catch(e) {
        return false
      }
    },
    checkRedeemScript(redeemScript) {
      let vm = this
      // store received multisig address
      vm.receivedRedeemScriptAddress = vm.getScriptAddress(redeemScript)
      // check if decompiled redeem script has op_multisig
      let decompiledRedeemScript = bitGoUTXO.script.decompile(redeemScript)
      let redeemScriptLength = decompiledRedeemScript.length
      if (decompiledRedeemScript[redeemScriptLength - 1] !== 174) {
        throw ("Not a multisig redeem script!")
      // sanity check, check consistency in number of possible signers
      } else if ( !vm.redeemScriptNSignersSanityCheck(decompiledRedeemScript) ) {
        throw ("Redeem script contains inconsistencies!")
      // sanity check, try and store all pubkeys and addresses involved
      } else if ( !vm.storeReceivedRedeemScriptAddresses(decompiledRedeemScript) ) {
        throw ("Could not store involved public keys and addresses!")
      // all checks passed
      }
    },
    onCameraDecode (qrcodeValue) {
      let vm = this
      // reset error state
      vm.cameraError = null
      // uncompress
      let hexString = vm.base64ToHex(qrcodeValue)
      // set received value
      vm.receivedRedeemInput = hexString
      // deactivate camera stream
      vm.toggleCamera = false
    },
    async onCameraInit (promise) {
      let vm = this
      try {
        await promise
      } catch (error) {
        if (error.name === 'NotAllowedError') {
          vm.cameraError = "ERROR: you need to grant camera access permission."
        } else if (error.name === 'NotFoundError') {
          vm.cameraError = "ERROR: no camera found on this device."
        } else if (error.name === 'NotSupportedError') {
          vm.cameraError = "ERROR: secure context required (HTTPS / localhost)."
        } else if (error.name === 'NotReadableError') {
          vm.cameraError = "ERROR: is the camera already in use?"
        } else if (error.name === 'OverconstrainedError') {
          vm.cameraError = "ERROR: installed cameras are not suitable."
        } else if (error.name === 'StreamApiNotSupportedError') {
          vm.cameraError = "ERROR: Stream API is not supported in this browser."
        }
      }
    },
    hexToBase64(hex) {
      let buffer = Buffer.from(hex, 'hex')
      let deflatedBuffer = pako.deflate(buffer)
      let compressedBuffer = Buffer.from(deflatedBuffer, 'hex')
      let compressedString = compressedBuffer.toString('base64')
      return compressedString
    },
    base64ToHex(base64) {
      let buffer = Buffer.from(base64, 'base64')
      let inflated = pako.inflate(buffer)
      let hexString = Buffer.from(inflated).toString('hex')
      return hexString
    },
  },

  computed: {
    isValidRedeemInput() {
      let vm = this
      let receivedHex = vm.receivedRedeemInput
      // reset state
      vm.receivedRedeemInputState = null
      vm.isFullySigned = null
      // reset redeem script stuff
      vm.receivedRedeemScript = vm.receivedRedeemScriptAddress = null
      vm.receivedRedeemScriptInvolvedAddresses = []
      vm.receivedRedeemScriptRequiredSignatures = null
      vm.signedSoFar = []
      vm.sendingAmount = null
      vm.destinationAddresses = []
      // reset wif (avoids "signature already exists" error during tests)
      vm.wif = null
      // reset error message
      vm.redeemInputValidationError = null
      // reset broadcast transaction variables
      vm.sentTransactionId = null
      vm.sendTransactionErrorMessage = null
      // don't perform checks on empty values
      if ( receivedHex.length < 1 ) {
        return null
      // check if alphanumeric
      } else if ( !receivedHex.match(/^[a-z0-9]+$/i) ) {
        vm.redeemInputValidationError = "Only letters and numbers, please."
        return vm.receivedRedeemInputState = false
      }
      // check if given input is a transaction
      let tx
      try {
        vm.showFromExplorer = false
        tx = bitGoUTXO.Transaction.fromHex(receivedHex, vm.network)
        // change network if necessary
        if (tx.version === 1) {
          vm.selectedNetwork = 'komodo_nonsapling'
        }
      } catch (e) {
        vm.showFromExplorer = true
        tx = false
      }
      // process transaction
      if (tx) {
        try {
          // check if all vins share the same script
          let last
          for (let vin = 0; vin < tx.ins.length; vin++) {
            let script = tx.ins[vin].script
            if (vin === 0) {
              last = script
            } else if (last !== script) {
              throw ("Inputs with different scriptSig are not supported!")
            }
          }
          // process transaction redeem script
          let scriptSig = last
          let decompiledScriptSig = bitGoUTXO.script.decompile(scriptSig)
          let dssl = decompiledScriptSig.length
          let redeemScript
          try {
            // this is a transaction that already has signatures in it
            redeemScript = Buffer.from(decompiledScriptSig[dssl-1])
            vm.checkRedeemScript(redeemScript)
            // check if fully signed
            // bitgo-utxo-lib uses zeroes as placeholders during the signing round
            // if a zero is found instead of a signature, then it's not fully signed
            vm.isFullySigned = true
            for ( let i=1; i < dssl-1; i++ ) {
              if (decompiledScriptSig[i] === 0) {
                vm.isFullySigned = false
              }
            }
            // get who signed so far
            if (vm.isFullySigned === false) {
              for ( let i=1; i < dssl-1; i++ ) {
                if ( decompiledScriptSig[i] ) {
                  let signer = vm.receivedRedeemScriptInvolvedAddresses[i-1]
                  vm.signedSoFar.push(signer)
                }
              }
            }
          } catch {
            // this is a raw transaction that hasn't been signed yet
            let decompFirstOut = bitGoUTXO.script.decompile(tx.outs[0].script)
            if (decompFirstOut[0] !== 106) {
              throw("This transaction doesn't look like made by this tool.")
            } else {
              redeemScript = decompFirstOut[1]
            }
            vm.checkRedeemScript(redeemScript)
            vm.isFullySigned = false
          }
          let redeemScriptHex = redeemScript.toString('hex')
          vm.receivedRedeemScript = redeemScriptHex
          // get destination address(es) and outgoing amount
          let sendingAmountSat = 0
          for (let o in tx.outs) {
            // get output script
            let outputScript = bitGoUTXO.script.decompile(tx.outs[o].script)
            // skip op_return outputs
            if (outputScript[0] === 106) {
              // throw an error if coins are being burned to opreturn
              if (tx.outs[o].value !== 0) {
                throw ("Burning coins is not allowed!")
              } else {
                continue
              }
            }
            // sum value being sent
            let value = tx.outs[o].value
            sendingAmountSat += value
            // get output address
            let address = bitGoUTXO.address.fromOutputScript(
              tx.outs[o].script, vm.network
            )
            // skip change output after substracting value from total
            if (vm.receivedRedeemScriptAddress === address) {
              sendingAmountSat -= value
              continue
            }
            // add to the list of destination addresses
            vm.destinationAddresses.push(address)
          }
          // push outgoing amount
          vm.sendingAmount = parseFloat(  // parseFloat eliminates trailing zeroes
            Number(sendingAmountSat / 100000000).toFixed(8)
          )
          // everything processed without error so far
          return vm.receivedRedeemInputState = true
        } catch (e) {
          vm.redeemInputValidationError = e
          return vm.receivedRedeemInputState = false
        }
      // process redeem script
      } else {
        try {
          let redeemScript = Buffer.from(receivedHex, 'hex')
          vm.checkRedeemScript(redeemScript)
          vm.receivedRedeemScript = receivedHex
          return vm.receivedRedeemInputState = true
        } catch (e) {
          vm.redeemInputValidationError = e
          return vm.receivedRedeemInputState = false
        }
      }
    },
    isValidUrl() {
      let vm = this
      let urlRegex = /^https:\/\/(\w|\d)+((\.{1})(\w|\d)+)*\/?$/
      if (vm.insightExplorerUrl.match(urlRegex)) {
        // update utxo list
        if (vm.showFromExplorer === true) {
          vm.getUtxos()
        }
        return true
      } else {
        // empty out utxo list
        vm.utxos = []
        return false
      }
    },
    insightExplorerUrlWSlash() {
      let vm = this
      if (!vm.insightExplorerUrl.match(/\/$/)) {
        return vm.insightExplorerUrl + '/'
      } else {
        return vm.insightExplorerUrl
      }
    },
    calculatedBalance() {
      let vm = this
      let total = 0
      if (vm.utxos !== null && vm.utxos.length > 0) {
        for (let utxo in vm.utxos) {
          total += vm.utxos[utxo].amount
        }
        return total
      } else if (vm.utxos !== null && vm.utxos.length === 0) {
        return 0
      } else {
        return null
      }
    },
    isValidAmount() {
      let vm = this
      let amount = Number(vm.selectedAmount)
      if (amount === null || amount === 0) {
        return null
      } else if (amount < 0) {
        return false
      } else {
        // get number of decimal places from original string
        let decimals = 0
        try {
          decimals = vm.selectedAmount.toString().split(".")[1].length
        } catch {
          // pass
        }
        if (decimals > 8) {
          return false
        } else if (amount > vm.calculatedBalance) {
          return false
        } else {
          return true
        }
      }
    },
    isValidDestinationAddr() {
      return this.isValidAddr(this.destinationAddress)
    },
    isValidWif() {
      let vm = this
      // reset error message
      vm.signatureErrorMsg = null
      if (!vm.wif || vm.wif.length === 0) {
        return null
      }
      try {
        bitGoUTXO.ECPair.fromWIF(vm.wif, vm.network)
        return true
      } catch {
        return false
      }
    },
    getTransaction() {
      let vm = this

      // reset state
      vm.isCompleteNow = null

      // declare variables
      let keyPair = vm.wif ? bitGoUTXO.ECPair.fromWIF(vm.wif, vm.network) : null
      let redeemScript = Buffer.from(vm.receivedRedeemScript, 'hex')
      let hashType = bitGoUTXO.Transaction.SIGHASH_ALL
      let inputList
      let builder

      // process fresh raw transaction
      if (vm.receivedRedeemInput === vm.receivedRedeemScript) {

        // instantiate transaction builder
        builder = new bitGoUTXO.TransactionBuilder(vm.network)

        // process available utxos
        let utxos = []
        for (let i in vm.utxos) {
          let utxo = vm.utxos[i]
          utxos.push({
            "txId": utxo.txid,
            "value": utxo.satoshis,
            "vout": utxo.vout
          })
        }

        // select utxos
        let targets = [{
          address: vm.destinationAddress,
          value: Math.round( Number(vm.selectedAmount) * 100000000 )
        }]
        let feeRate = 55
        /* eslint prefer-const: "off" */
        let { inputs, outputs } = coinSelect(utxos, targets, feeRate);
        inputList = inputs

        // add inputs
        let inputValues = []
        for (let i in inputList) {
          builder.addInput(
            inputList[i].txId,
            inputList[i].vout
          )
          inputValues.push(inputList[i].value)  // used on sapling transactions
        }

        // we need to store the redeem script in an op_return output
        // in order to share with the signing client
        let redeemScriptOpreturn = bitGoUTXO.script.fromASM(
          'OP_RETURN '+ vm.receivedRedeemScript
        )
        builder.addOutput(redeemScriptOpreturn, 0)

        // sapling specific
        if (vm.selectedNetwork === 'komodo') {  // TODO: don't depend on gui stuff
          // set sapling stuff if necessary
          builder.setVersion(bitGoUTXO.Transaction.ZCASH_SAPLING_VERSION)  // 4
          builder.setVersionGroupId(parseInt('0x892F2085', 16))
          // store input values in opreturn
          let dataBuffer = Buffer.from(JSON.stringify(inputValues))
          let dataScript = bitGoUTXO.script.nullData.output.encode(dataBuffer)
          builder.addOutput(dataScript, 0)
        }

        // add outputs
        for (let o in outputs) {
          builder.addOutput(
            (outputs[o].address || vm.receivedRedeemScriptAddress),
            outputs[o].value
          )
        }

      } else {  // process an existing transaction

        // process received transaction
        let tx = bitGoUTXO.Transaction.fromHex(
          vm.receivedRedeemInput, vm.network
        )
        inputList = tx.ins

        // analyze the first output (useful for detecting fresh raw transactions)
        let decompFirstOut = bitGoUTXO.script.decompile(tx.outs[0].script)
        let firstOpreturn
        if (decompFirstOut[0] !== 106) {
          throw("This transaction doesn't look like made by this tool.")
        } else {
          firstOpreturn = decompFirstOut[1]
        }

        // check if this is a fresh raw transaction
        let fopret = firstOpreturn.toString('hex')
        let rs = redeemScript.toString('hex')
        if ( fopret === rs ) {
          // remove the temporary opreturn where we stored the redeem script
          tx.outs.shift()
          // try and emulate normal bitgo-utxo-lib behavior for multisig
          // e.g. (for a 2 of 3): 0 0 0 0 <redeem script>
          // (first mandatory zero plus m zeroes)
          let zeroes = ''
          vm.receivedRedeemScriptInvolvedAddresses.map(function(){
            zeroes += 'OP_0 '
          })
          for (let i in inputList) {
            let temporaryScriptSig = 'OP_0 ' + zeroes + rs
            tx.ins[i].script = bitGoUTXO.script.fromASM(temporaryScriptSig)
          }
        }

        // sapling specific stuff
        if (vm.selectedNetwork === 'komodo') {  // TODO: don't depend on gui stuff
          // we have stored (at transaction creation stage) the input values
          // (amounts) in an opreturn (otherwise they'd be lost after sharing)
          let opreturnValues = JSON.parse(
            bitGoUTXO.script.nullData.output.decode(
              tx.outs[0].script
            ).toString()
          )
          // add input values (amounts) to inputList
          for (let i in inputList) {
            inputList[i].value = opreturnValues[i]
          }
        }

        // instantiate builder
        builder = new bitGoUTXO.TransactionBuilder.fromTransaction(
          tx, vm.network
        )
      }

      if (keyPair) {
        // sign transaction
        for (let i in inputList) {
          builder.sign(
            Number(i),
            keyPair,
            redeemScript,
            hashType,
            "value" in inputList[i] ? inputList[i].value : null  // used on sapling transactions
          )
        }
      }

      // build transaction
      let Tx
      let reqSign = vm.receivedRedeemScriptRequiredSignatures
      let howManySigned = vm.signedSoFar.length + 1
      if ( howManySigned === reqSign ) {  // completely signed transaction
        Tx = builder.build()
        vm.isCompleteNow = true
      } else {  // raw or incomplete transaction
        Tx = builder.buildIncomplete()
        vm.isCompleteNow = false
      }

      // return signed transaction
      return Tx.toHex()
    },
    getTransactionCompressed() {
      let vm = this
      let hex = vm.getTransaction
      return vm.hexToBase64(hex)
    },
    origin() {
      return location.origin
    },
  }
}
</script>


