<template>
  <div class="mainpage" autocomplete="off" autocorrect="off" autocapitalize="off" spellcheck="false">

    <h3>Create a new multisig address.</h3>
    <br>
    Maximum number of signers:

    <div class="inputField">
      <b-form-select
        class="mb-2 mr-sm-2 mb-sm-0 formSelect"
        v-model="pubkeysNtotal"
        :options="pubkeysNtotalOptions"
        id="inline-form-custom-select-pref"
      >
      </b-form-select>
    </div>

    <div class="inputField">
      Public keys of all possible signers:
      <b-form-input v-for="field in pubkeyListFields"
        class="inputField"
        placeholder="Signer public key"
        v-model="pubkeyList[field]"
        :key="field"
        :state="inputFieldState(field)"
      >
      </b-form-input>
    </div>

    <div class="inputField">
      <h6>Minimum required number of signers:</h6>
      <b-form-select
        class="mb-2 mr-sm-2 mb-sm-0 formSelect"
        v-model="pubkeysNrequired"
        :options="pubkeysNrequiredOptions"
        id="inline-form-custom-select-pref"
      >
      </b-form-select>
    </div>

    <div v-if="this.allFieldStatesValid() && this.getCreatedRedeemScript">

      <br>
      <h3 class="inputField">Multisig Address:</h3>
      <b-row>
        <b-col>
          <b-form-input
            readonly
            style="text-align: center;"
            type="text"
            v-model="createdMultiSigData.address"
          >
          </b-form-input>
        </b-col>
        <b-col class="clipboard-button">
          <b-button variant="outline-primary"
            v-clipboard:copy="createdMultiSigData.address"
            @click="$bvToast.toast('🗸 Copied to the clipboard!', {
                toaster: 'b-toaster-top-center',
                title: 'Multisig Address',
                autoHideDelay: 2000,
                variant: 'success',
              })"
          >📋</b-button>
        </b-col>
      </b-row>

      <h3 class="inputField">Redeem Script:</h3>

      <show-qr
        :string="createdMultiSigData.redeemScript"
        :stringCompressed="createdMultiSigData.redeemScriptCompressed"
        :showValidator="true"
        :size="400"
      />

    </div>

    <div v-else-if="this.allFieldStatesValid() && creationErrorMessage">
      <p class="creationError">{{creationErrorMessage}}</p>
    </div>
  </div>
</template>


<!-- script section -->
<script>
import bitGoUTXO from 'bitgo-utxo-lib'
import * as pako from 'pako'
import ShowQr from '@/components/ShowQr.vue'


export default {
  name: 'NewAddressTab',

  props: {
    msg: String,
    defaultNetwork: String,
  },

  components: {
    //QrcodeStream, // TODO: add QR reader for public keys
    ShowQr,
  },

  data() {
    return {
      pubkeysNtotal: 3,
      pubkeysNtotalOptions: [2, 3, 4, 5, 6, 7],
      pubkeysNrequired: 2,
      pubkeysNrequiredOptions: [1, 2, 3],
      pubkeyListFields: [0, 1, 2],
      pubkeyList: {},
      pubkeyListStates: {},
      createdMultiSigData: null,
      creationErrorMessage: null,
      selectedNetwork: null,
    }
  },

  created: function() {
    this.selectedNetwork = this.defaultNetwork
  },

  watch: {
    pubkeysNtotal: function() {
      let vm = this
      // update number of fields according to selected number of pubkeys
      vm.pubkeyListFields = []  // first reset
      for (let i = 0; i < vm.pubkeysNtotal; i++) {
        vm.pubkeyListFields.push(i)
      }
      // make pubkeysNrequired less or equal than pubkeysNtotal
      vm.pubkeysNrequiredOptions = []  // first reset
      for (let i = 0; i < vm.pubkeysNtotal; i++) {
        vm.pubkeysNrequiredOptions.push(i+1)
      }
    },
  },

  methods: {
    getScriptAddress(scriptBuffer) {
      let vm = this
      let scriptPubKey = bitGoUTXO.script.scriptHash.output.encode(
        bitGoUTXO.crypto.hash160(scriptBuffer)
      )
      let address = bitGoUTXO.address.fromOutputScript(
        scriptPubKey,
        vm.network
      )
      return address
    },
    getMultiSig(requiredSignersNum, pubkeysHex) {
      let vm = this
      let pubkeys = pubkeysHex.map(
        function (hex) { return Buffer.from(hex, 'hex') }
      )
      let redeemScript = bitGoUTXO.script.multisig.output.encode(
        requiredSignersNum,
        pubkeys
      )
      let address = vm.getScriptAddress(redeemScript)
      let redeemScriptHex = redeemScript.toString('hex')
      // compress for qr code
      let deflatedRS = pako.deflate(redeemScript)
      let compressedRS = Buffer.from(deflatedRS, 'hex').toString('base64')
      return {
        'redeemScript': redeemScriptHex,
        'redeemScriptCompressed': compressedRS,
        'address': address,
      }
    },
    inputFieldState (field) {
      let vm = this
      let thisField = vm.pubkeyList[field] || ""
      // validation code
      let output = null
      if (thisField.length > 0) {
        output = false
        if (thisField.length === 66) {
          try {
            output = true
          } catch(e) {
            // TODO: handle error
          }
        }
      }
      vm.pubkeyListStates[field] = output
      return output
    },
    allFieldStatesValid () {
      let vm = this
      let validStates = 0
      for (let field in vm.pubkeyListFields) {
        if (vm.pubkeyListStates[field] === true) {
          validStates += 1
        }
      }
      if (validStates === vm.pubkeysNtotal) {
        return true
      } else {
        return false
      }
    },
  },

  computed: {
    network() {
      return bitGoUTXO.networks[this.selectedNetwork]
    },
    getCreatedRedeemScript() {
      let vm = this
      // update relevantPubKeys
      let relevantPubKeys = []
      for (let i = 0; i < vm.pubkeysNtotal; i++) {
        if (vm.pubkeyList[i] !== undefined && vm.pubkeyList[i].length > 0) {
          relevantPubKeys.push(vm.pubkeyList[i])
        }
      }
      // abort if form not filled yet
      if (relevantPubKeys.length < vm.pubkeysNtotal) {
        vm.createdMultiSigData = null
        return false
      }
      try {
        vm.creationErrorMessage = null
        vm.createdMultiSigData = vm.getMultiSig(
          vm.pubkeysNrequired,
          relevantPubKeys
        )
        return true
      } catch (e) {
        vm.createdMultiSigData = null
        vm.creationErrorMessage = e
        return false
      }
    },
  },

}
</script>


